import PropTypes from 'prop-types';
import React from 'react';

import { mapDynamicFieldToName } from '@mssgme/shared';
import { cls } from '@mssgme/helpers';
import { Input, Textarea } from '@mssgme/ui';
import { TextField } from '../Inputs';

import styles from './DynamicFormInputs.scss';

const handleEnterKey = (e) => {
    if (e.key === 'Enter') {
        e.preventDefault();
    }
};

export const DynamicFormInput = ({ index, input, as, type, defaultMaxSize, theme }) => {
    const maxLength = input.size || defaultMaxSize;
    const isTextField = maxLength <= 250;

    return (
        <TextField
            name={mapDynamicFieldToName(input, index)}
            label={input.label}
            required={input.required}
            maxLength={maxLength}
            wrapAs={as}
            component={isTextField ? Input : Textarea}
            type={type || 'text'}
            onKeyPress={isTextField ? handleEnterKey : undefined}
            theme={theme}
        />
    );
};

DynamicFormInput.propTypes = {
    input: PropTypes.shape({
        _id: PropTypes.string,
        label: PropTypes.string,
        required: PropTypes.bool,
        size: PropTypes.number,
    }).isRequired,
    index: PropTypes.number.isRequired,
    as: PropTypes.oneOfType([PropTypes.string, PropTypes.elementType]),
    type: PropTypes.string,
    defaultMaxSize: PropTypes.number.isRequired,
    theme: PropTypes.object,
};

const inputTypes = {
    name: 'text',
    email: 'email',
    phone: 'tel',
    text: 'text',
};

export const DynamicFormInputs = ({ block, theme, defaultMaxSize, ...rest }) => {
    return (
        <ul {...cls([styles.root, styles[theme.gridMode]], rest)}>
            {block.items.map((item, index) => (
                <DynamicFormInput
                    as="li"
                    key={item._id || index}
                    index={index}
                    input={item}
                    type={inputTypes[item.type]}
                    defaultMaxSize={defaultMaxSize}
                    theme={theme}
                />
            ))}
        </ul>
    );
};

DynamicFormInputs.propTypes = {
    block: PropTypes.shape({
        items: PropTypes.arrayOf(
            PropTypes.shape({
                _id: PropTypes.string,
                label: PropTypes.string,
                required: PropTypes.bool,
                size: PropTypes.number,
            })
        ).isRequired,
    }).isRequired,
    theme: PropTypes.object.isRequired,
    defaultMaxSize: PropTypes.number.isRequired,
};
