import PropTypes from 'prop-types';
import React, { useCallback, useEffect, useMemo } from 'react';
import ReactDom from 'react-dom';
import { useHistory } from 'react-router';
// import { FiChevronLeft, FiChevronRight, FiX } from 'react-icons/fi';
import { motion, startAnimation, useMotionValue } from 'framer-motion';

import { Swiper } from '@mssgme/ui';
import { usePageData, usePageTheme } from '../../../hooks';
import { ServiceContent } from '../../../UI';

import styles from './ServicesModal.scss';

const animate = (from, to, transition) => {
    void startAnimation('', from, to, transition);

    return {
        stop: () => from.stop(),
    };
};

export function ServicesModal({
    services,
    contacts,
    activeIndex,
    interactive,
    onClose,
    onClosing,
    themeStyles,
    handleHash,
}) {
    const page = usePageData();
    const {
        style: { backgroundColor },
    } = usePageTheme(true, true);
    const history = useHistory();
    const opacity = useMotionValue(0);
    // const navigationPrevRef = React.useRef(null);
    // const navigationNextRef = React.useRef(null);
    const hasMultipleServices = services.length > 1;
    // const isMobile = useIsMobile();

    const contactBlocks = useMemo(
        () =>
            contacts
                .filter(({ enabled }) => enabled)
                .map(({ blockId }) => page.blocks.find(({ _id }) => _id === blockId)),
        [page, contacts]
    );

    // const handleInit = useCallback(
    //     (swiper) => {
    //         swiper.params.navigation.prevEl = navigationPrevRef.current;
    //         swiper.params.navigation.nextEl = navigationNextRef.current;
    //         swiper.navigation.update();
    //     },
    //     [navigationPrevRef, navigationNextRef]
    // );
    const handleSlideChange = useCallback(
        (swiper) => {
            if (handleHash) {
                history.replace('#' + services[swiper.realIndex]._id);
            }
        },
        [handleHash, services]
    );
    const handleClose = useCallback(() => {
        if (handleHash) {
            history.replace(history.location.pathname);
        }

        onClosing && onClosing();

        animate(opacity, 0, {
            type: 'spring',
            stiffness: 100,
            onComplete: onClose,
        });
    }, [onClose, onClosing, handleHash]);

    useEffect(() => {
        const controls = animate(opacity, 1, {
            type: 'spring',
            stiffness: 100,
        });

        return controls.stop;
    }, []);

    return ReactDom.createPortal(
        <motion.div className={styles.root} style={{ opacity, backgroundColor }}>
            {/*<div className={styles.header} style={{ color: themeStyles.color }}>*/}
            {/*    <div className={styles.closeButton} onClick={handleClose}>*/}
            {/*        <FiX size={24} />*/}
            {/*    </div>*/}
            {/*    /!*{hasMultipleServices && (*!/*/}
            {/*    /!*    <div className={styles.controls} style={{ display: isMobile ? 'none' : null }}>*!/*/}
            {/*    /!*        <div className={styles.control} ref={navigationPrevRef}>*!/*/}
            {/*    /!*            <FiChevronLeft size={24} />*!/*/}
            {/*    /!*        </div>*!/*/}
            {/*    /!*        <div className={styles.control} ref={navigationNextRef}>*!/*/}
            {/*    /!*            <FiChevronRight size={24} />*!/*/}
            {/*    /!*        </div>*!/*/}
            {/*    /!*    </div>*!/*/}
            {/*    /!*)}*!/*/}
            {/*    <div className={styles.closeButton} style={{ opacity: 0, pointerEvents: 'none' }}>*/}
            {/*        <FiX size={24} />*/}
            {/*    </div>*/}
            {/*</div>*/}
            <div className={styles.content}>
                <Swiper
                    className={styles.swiper}
                    allowTouchMove={hasMultipleServices}
                    initialSlide={activeIndex}
                    noSwipingClass={styles.noSwiping}
                    speed={600}
                    pagination={false}
                    // watchOverflow={services.length > 1}
                    // loop={services.length > 1}
                    onInit={handleSlideChange}
                    onSlideChange={handleSlideChange}
                >
                    {services.map((service, index) =>
                        Object.assign(
                            ({ isActive, isPrev, isNext }) => (
                                <ServiceContent
                                    themeStyles={themeStyles}
                                    service={service}
                                    contacts={contactBlocks}
                                    isActive={isActive}
                                    isPrev={isPrev}
                                    isNext={isNext}
                                    interactive={interactive}
                                    onClose={handleClose}
                                />
                            ),
                            { key: service._id || index }
                        )
                    )}
                </Swiper>
            </div>
        </motion.div>,
        document.querySelector('#page_root, #mainRoot')
    );
}

ServicesModal.propTypes = {
    services: PropTypes.array.isRequired,
    contacts: PropTypes.array.isRequired,
    activeIndex: PropTypes.number.isRequired,
    onClose: PropTypes.func.isRequired,
    onClosing: PropTypes.func,
    themeStyles: PropTypes.object.isRequired,
    handleHash: PropTypes.bool,
    interactive: PropTypes.bool,
};

ServicesModal.defaultProps = {
    handleHash: true,
    interactive: true,
};
